@import "../../scss/variables";

.container{
   
    h1{
        margin-bottom: 1rem;
        font-weight: 200;
        font-size: 3rem;
    }
    .show {
        margin: 1rem 0;
        font-size: 16px;
        font-weight: 200;
        letter-spacing: 1px;
        padding: 13px 20px 13px;
        outline: 0;
        border: 1px solid $borderColor;
        cursor: pointer;
        position: relative;
        background-color: rgba(0, 0, 0, 0);
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        width: 50%;
        z-index: 0;
    
        &:after {
          content: "";
          background-color: $textColor;
          width: 100%;
          z-index: -1;
          position: absolute;
          height: 100%;
          top: 7px;
          left: 7px;
          transition: 0.2s;
          opacity: 0.5;
        }
        &:hover:after{
            top: 0;
            left: 0;
        }
    p{
        padding: 3rem;
    }
}
}